raul-modal {
  display: block;
  font-size: 0.875rem;
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  raul-modal .r-modal__container {
    flex: auto;
  }

  raul-modal raul-modal-body {
    flex: auto;
  }
}

raul-modal-header {
  flex: 0 1 auto;
  margin-bottom: 0.5rem;
}

raul-modal-body {
  flex-direction: column;
  text-align: center;
  overflow-y: auto;
  flex: 1 1 auto;
  scrollbar-width: thin;
  scrollbar-color: #d3d3d3 #fff;
  scrollbar-base-color: #theme "colors.gray-light";
  scrollbar-face-color: #aaa;
  scrollbar-3dlight-color: #fff;
  scrollbar-highlight-color: ;
  scrollbar-track-color: #fff;
  scrollbar-arrow-color: #fff;
  scrollbar-shadow-color: #fff;
  scrollbar-dark-shadow-color: #fff;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}

raul-modal-body::-webkit-scrollbar {
  width: 0.25rem;
}

raul-modal-body::-webkit-scrollbar-thumb {
  background-color: #C6CCD0;
  height: 8rem;
  opacity: 0.8;
}

.r-modal {
  display: none;
  z-index: 10000;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.r-modal--visible {
  display: block;
}

.r-modal--media {
  width: 704px;
  padding: 0;
}

@media (max-width: 640px) {
  .r-modal--media {
    width: 100%;
    height: 100%;
  }
}

.r-modal--media.r-modal-header {
  justify-content: center;
}

.r-modal--media.r-modal-header__close {
  display: block;
}

.r-modal__overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  align-items: center;
  justify-content: center;
  height: 100%;
  -webkit-animation: mfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
  animation: mfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
  z-index: 0;
  will-change: transform;
  background: rgba(0, 0, 0, 0.6);
}

.r-modal__container {
  background-color: #fff;
  padding-top: 2rem;
  padding-bottom: 2rem;
  padding-right: 1rem;
  padding-left: 1rem;
  border-radius: 0.25rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  display: flex;
  z-index: 10000;
  position: absolute;
  flex: 0 1 auto;
  -webkit-animation: mslideIn 0.3s cubic-bezier(0, 0, 0.2, 1);
  animation: mslideIn 0.3s cubic-bezier(0, 0, 0.2, 1);
  will-change: transform;
  max-height: calc(100vh - 80px);
  width: 360px;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}

@media (max-width: 640px) {
  .r-modal__container {
    top: initial;
    bottom: 2.5rem;
    transform: translate(-50%, 0);
  }
}

.r-modal__container--normal .r-modal-header__content {
  justify-content: center;
  width: 100%;
  text-align: center;
}

.r-modal__container--normal .r-modal-header__close {
  display: none;
}

.r-modal__container--normal .r-modal-body {
  flex: 1 1 0%;
  margin-bottom: 1rem;
}

.r-modal__container--normal .r-modal-footer {
  justify-content: center;
}

.r-modal__container--media {
  width: 704px;
  border-radius: 0.25rem;
  max-height: 100%;
  padding-left: 2rem;
  padding-right: 2rem;
}

@media (max-width: 640px) {
  .r-modal__container--media {
    width: 100%;
    height: 100%;
  }
}

.r-modal__container--media .r-modal-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.r-modal__container--media .r-modal-header__content {
  flex: 1 1 0%;
  justify-content: flex-start;
}

.r-modal__container--media .r-modal-header__close {
  display: block;
}

.r-modal__container--media .r-modal-body {
  flex: 1 1 0%;
  margin-bottom: 0;
}

.r-modal .r-modal-header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.r-modal .r-modal-header__content {
  display: flex;
  flex-direction: column;
}

.r-modal .r-modal-header__content h2 {
  font-size: 1.25rem;
  font-weight: 600;
  line-height: 1.25;
  margin-bottom: 0.5rem;
  margin-bottom: 0;
}

.r-modal .r-modal-header__content p {
  font-size: 0.875rem;
  line-height: 1.25;
  margin-bottom: 0.5rem;
  color: rgba(55, 71, 79, 0.8);
  margin-bottom: 0;
}

.r-modal .r-modal-header__close:focus {
  outline: none;
}

.r-modal .r-modal-header__close:focus .r-modal-header__close--focus {
  outline: 1px solid #0076CC;
}

.r-modal .r-modal-header__close--focus {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.r-modal .r-modal-header__close--focus:focus {
  outline: none;
}

.r-modal .r-modal-body {
  display: flex;
  flex: 1 1 0%;
  justify-content: center;
  align-items: center;
  color: rgba(55, 71, 79, 0.8);
  width: 100%;
  padding-left: 1rem;
  padding-right: 1rem;
}

.r-modal .r-modal-body img {
  flex: 1 1 0%;
}

.r-modal .r-modal-footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

@media (max-width: 640px) {
  .r-modal .r-modal-footer {
    flex-direction: column;
  }

  .r-modal .r-modal-footer raul-button {
    padding: 0;
  }

  .r-modal .r-modal-footer raul-button:not(:first-child) {
    padding-top: 1rem;
  }
}

@media (min-width: 640px) {
  .r-modal .r-modal-footer raul-button {
    flex: 1 1 0%;
  }

  .r-modal .r-modal-footer raul-button:not(:first-child) {
    padding-left: 1rem;
  }
}

@-webkit-keyframes mfadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes mfadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes mslidein {
  from {
    transform: translatey(15%);
  }

  to {
    transform: translatey(0);
  }
}

@keyframes mslidein {
  from {
    transform: translatey(15%);
  }

  to {
    transform: translatey(0);
  }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  raul-modal .r-modal__container {
    flex: auto;
  }

  raul-modal raul-modal-body {
    flex: auto;
  }
}